import styles from "./QuestElement.module.css";
import { useState } from "react";
export function QuestElement({ number, title, description, large }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div
      className={styles["container"]}
      onClick={() => (isOpen ? setIsOpen(false) : setIsOpen(true))}
    >
      <div className={`${styles.title} ${isOpen ? styles.isopen : ""}`}>
        {number}. {title}{" "}
        <img className={styles.icon} src="/icons/plus.svg" alt="" />
      </div>
      <p
        className={`${styles.description} ${isOpen && large ? styles.large : ""} ${isOpen ? styles.descriptionOpen : ""}`}
      >
        {description}
      </p>
    </div>
  );
}
