import styles from "./Modal.module.css";
import registerStyles from "./ModalRegister.module.css";
import { useState, useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useAuth } from "../hooks/useAuth";
import { Loader } from "../components/Loader";

export function ModalRegister() {
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [showPassword2, setShowPassword2] = useState(false);
  const [step, setStep] = useState("email");
  const { register, setError, handleSubmit, watch, formState } = useForm({
    mode: "onSubmit",
    reValidateMode: "onSubmit",
  });
  const { errors } = formState;
  const navigate = useNavigate();
  const { register: sendRegister } = useAuth();
  const [successfullyRegister, setSuccessfullyRegister] = useState(false);

  const passwordError = errors.password?.message;
  const confirmError = errors.confirm?.message;
  const emailError = errors.root?.server?.message || errors.email?.message;

  const hasEmailError = Boolean(emailError);
  const hasPasswordError = Boolean(passwordError);
  const hasConfirmError = Boolean(confirmError);

  const [emailErrorText, setEmailErrorText] = useState("");
  const [passwordErrorText, setPasswordErrorText] = useState("");
  const [confirmErrorText, setConfirmErrorText] = useState("");

  const onSubmit = async ({ email, password }) => {
    setIsLoading(true);
    const res = await sendRegister({
      registerEmail: email,
      registerPassword: password,
    });

    if (!res.ok) {
      setError("email", {
        type: "server",
        message: `${res.message === "user with this email already exists" ? "     " : " "}`,
      });

      setStep("email");
      setIsLoading(false);
      return;
    }
    setSuccessfullyRegister(true);
    setIsLoading(false);
    setTimeout(() => navigate("/login"), 2500);
  };

  useEffect(() => {
    if (emailError) {
      setEmailErrorText(emailError);
    }
  }, [emailError]);

  useEffect(() => {
    if (passwordError) {
      setPasswordErrorText(passwordError);
    }
  }, [passwordError]);

  useEffect(() => {
    if (confirmError) {
      setConfirmErrorText(confirmError);
    }
  }, [confirmError]);

  return (
    <div className={styles.wrapper}>
      {isLoading && <Loader />}
      <div className={styles["wrapper_header"]}>
        <h2 onClick={() => navigate("/")}>JULIIAZHIVAEVA</h2>
        <button onClick={() => navigate("/")}>
          <svg
            width="28"
            height="28"
            viewBox="0 0 28 28"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M0.43868 0.43868C0.71993 0.157779 1.10118 0 1.49868 0C1.89618 0 2.27743 0.157779 2.55868 0.43868L26.5587 24.4387C26.7061 24.576 26.8243 24.7416 26.9062 24.9256C26.9882 25.1096 27.0323 25.3082 27.0359 25.5096C27.0394 25.711 27.0024 25.9111 26.9269 26.0979C26.8515 26.2847 26.7392 26.4543 26.5968 26.5968C26.4543 26.7392 26.2847 26.8515 26.0979 26.9269C25.9111 27.0024 25.711 27.0394 25.5096 27.0359C25.3082 27.0323 25.1096 26.9882 24.9256 26.9062C24.7416 26.8243 24.576 26.7061 24.4387 26.5587L0.43868 2.55868C0.157779 2.27743 0 1.89618 0 1.49868C0 1.10118 0.157779 0.71993 0.43868 0.43868Z"
              fill="#FBE0F3"
            />
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M26.5587 0.43868C26.8396 0.71993 26.9974 1.10118 26.9974 1.49868C26.9974 1.89618 26.8396 2.27743 26.5587 2.55868L2.55872 26.5587C2.27437 26.8236 1.89827 26.9679 1.50967 26.961C1.12107 26.9542 0.750299 26.7968 0.475473 26.5219C0.200646 26.2471 0.0432229 25.8763 0.0363665 25.4877C0.0295101 25.0991 0.173756 24.723 0.438716 24.4387L24.4387 0.43868C24.72 0.157779 25.1012 0 25.4987 0C25.8962 0 26.2775 0.157779 26.5587 0.43868Z"
              fill="#FBE0F3"
            />
          </svg>
        </button>
      </div>
      <div className={styles.container}>
        <div className={styles.buttons}>
          <NavLink
            to="/login"
            className={({ isActive }) =>
              `${styles["nav-button"]} ${isActive ? styles.selected : ""}`
            }
          >
            
          </NavLink>
          <NavLink
            to="/register"
            className={({ isActive }) =>
              `${styles["nav-button"]} ${isActive ? styles.selected : ""}`
            }
          >
            
          </NavLink>
        </div>
        {step === "email" ? (
          <form
            onSubmit={handleSubmit(() => setStep("password"))}
            className={registerStyles["register-form"]}
            noValidate
          >
            <h3 className={styles.title}>   </h3>
            <input
              type="email"
              placeholder=""
              {...register("email", {
                required: " ",
                pattern: {
                  value: /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/,
                  message: "  ,  - ivanova@mail.ru",
                },
              })}
              className={`${styles.input} ${errors.email?.message ? registerStyles["error_border"] : ""}`}
            />
            <p
              className={`${registerStyles.error} ${hasEmailError ? registerStyles.visible : ""}`}
            >
              {emailErrorText}
            </p>
            <button type="submit" className={styles.btn}>
              
              <svg
                width="21"
                height="19"
                viewBox="0 0 21 19"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M19.3862 0.815155C19.3422 0.315387 18.8232 -0.0471807 18.2269 0.00533654L8.50958 0.861155C7.91328 0.913672 7.46557 1.36139 7.50958 1.86116C7.5536 2.36092 8.07267 2.72349 8.66897 2.67097L17.3066 1.91025L17.9441 9.14952C17.9881 9.64929 18.5072 10.0119 19.1035 9.95934C19.6998 9.90682 20.1475 9.4591 20.1035 8.95934L19.3862 0.815155ZM0.819336 18.3975L1.63915 18.9701L19.1264 1.48287L18.3066 0.910246L17.4867 0.337618L-0.000479519 17.8248L0.819336 18.3975Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
            <button className={styles["agreement"]}>
                     
            </button>
          </form>
        ) : successfullyRegister === true ? (
          <div className={registerStyles["successfully-register"]}></div>
        ) : (
          <form
            className={registerStyles["register-form"]}
            onSubmit={handleSubmit(onSubmit)}
            noValidate
          >
            <h3 className={registerStyles["title_register_password"]}>
                 
            </h3>

            <div className={registerStyles["input_wrapper-register"]}>
              <input
                type={showPassword ? "text" : "password"}
                placeholder=""
                {...register("password", {
                  required: " ",
                  minLength: {
                    value: 6,
                    message: "     6 ",
                    pattern: {
                      value: /^[A-Za-z0-9]+$/,
                      message:
                        "       ",
                    },
                  },
                })}
                className={`${registerStyles["input_register_password"]} ${errors.password?.message ? registerStyles["error_border"] : ""}`}
              />
              <button
                onClick={() => setShowPassword(!showPassword)}
                type="button"
                className={styles["toggle"]}
              >
                <img src="/icons/eye.svg" alt="" />
              </button>
            </div>
            <p
              className={`${registerStyles.error} ${hasPasswordError ? registerStyles.visible : ""} ${registerStyles["first-error"]}`}
            >
              {passwordErrorText}
            </p>

            <div className={registerStyles["input_wrapper-register"]}>
              <input
                type={showPassword2 ? "text" : "password"}
                placeholder=" "
                {...register("confirm", {
                  validate: (value) =>
                    value === watch("password") || "  ",
                  pattern: {
                    value: /^[A-Za-z0-9]+$/,
                    message:
                      "       ",
                  },
                })}
                className={`${registerStyles["input_register_password"]} ${errors.confirm?.message ? registerStyles["error_border"] : ""}`}
              />
              <button
                onClick={() => setShowPassword2(!showPassword2)}
                type="button"
                className={styles["toggle"]}
              >
                <img src="/icons/eye.svg" alt="" />
              </button>
            </div>
            <p
              className={`${registerStyles.error} ${hasConfirmError ? registerStyles.visible : ""}`}
            >
              {confirmErrorText}
            </p>

            <button type="submit" className={styles.btn}>
              
              <svg
                width="21"
                height="19"
                viewBox="0 0 21 19"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M19.3862 0.815155C19.3422 0.315387 18.8232 -0.0471807 18.2269 0.00533654L8.50958 0.861155C7.91328 0.913672 7.46557 1.36139 7.50958 1.86116C7.5536 2.36092 8.07267 2.72349 8.66897 2.67097L17.3066 1.91025L17.9441 9.14952C17.9881 9.64929 18.5072 10.0119 19.1035 9.95934C19.6998 9.90682 20.1475 9.4591 20.1035 8.95934L19.3862 0.815155ZM0.819336 18.3975L1.63915 18.9701L19.1264 1.48287L18.3066 0.910246L17.4867 0.337618L-0.000479519 17.8248L0.819336 18.3975Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
            <button className={styles["agreement"]}>
                     
            </button>
          </form>
        )}
      </div>
    </div>
  );
}
