import { Header } from "../components/Header";
import { Body } from "../components/Body";
import { Purpose } from "../components/Purpose";
import { WhoWant } from "../components/WhoWant";
import { Training } from "../components/Training";
import { Diet } from "../components/Diet";
import { GetRid } from "../components/GetRid";
import { Result } from "../components/Result";
import { Qestions } from "../components/Questions";
import { Footer } from "../components/Footer";
import { Cart } from "../components/Cart";
import { useSearchParams, useNavigate } from "react-router-dom";
import { useEffect } from "react";
import { useCartStatus } from "../store/store";
export function Home() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const cart = useCartStatus();

  useEffect(() => {
    const token = searchParams.get("token");
    if (token) {
      navigate(`/recover?token=${token}`, { replace: true });
    }

    if (window.location.hash) {
      const id = window.location.hash.substring(1);
      const el = document.getElementById(id);

      if (el) {
        el.scrollIntoView({ behavior: "smooth" });
      }
    }
  }, [searchParams, navigate]);

  useEffect(() => {
    if (cart) {
      document.getElementById("main-page").setAttribute("inert", "");
    } else {
      document.getElementById("main-page").removeAttribute("inert", "");
    }
  }, [cart]);

  return (
    <>
      <main id="main-page">
        <Header />
        <Body />
        <Purpose />
        <WhoWant />
        <Training />
        <GetRid />
        <Result />
        <Diet />
        <Qestions />
        <Footer />
      </main>
      <Cart />
    </>
  );
}
